@import 'bootstrap.less';
// 方法:设置cfec按钮样式
.box-shadow(@w, @h, @r, @color) {
    -webkit-box-shadow: @w @h @r @color;
    -moz-box-shadow: @w @h @r @color;
    box-shadow: @w @h @r @color;
}

// Body reset
html {
    height: 100%;
    font-size: 10px;

    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

body {
    position: relative;
    display: block;
    min-width: 1200px;
    height: 100%;
    font-family: @font-family-base;
    font-size: @font-size-base;
    line-height: @line-height-base;
    color: @text-color;
    background-color: #f1f2f7;
}

.midcontainer {
    width: 100%;
    height: 100%;
    background-color: #f1f2f7;
}

.oms-container {
    padding: 20px;
    background-color: #f1f2f7;
    .oms-content {
        padding-bottom: 20px;
        overflow-x: hidden;
        background-color: #fff;
        .table-cfec>table>tbody>tr>td {
            text-align: left;
            vertical-align: top;
            p {
                margin: 0;
            }
        }
    }
}

form {
    input.error-explain {
        background: #fff2f2;
        border-color: #e50000;
    }
    select.error-explain,
    textarea.error-explain {
        border-color: #e50000 !important;
    }
    em.error-explain,
    em.valid-explain {
        display: inline-block;
        height: 1.4em;
        padding-left: 18px;
        margin-top: 7px;
        margin-left: 10px;
        font-size: 12px;
        font-style: normal;
        line-height: 1.4em;
        word-break: break-word;
        vertical-align: top;
        zoom: 1;
    }
    em.error-explain {
        color: #e50000;
        background: url(/view/images/validate.png) no-repeat -186px -1px;
    }
    em.valid-explain {
        background: url(/view/images/validate.png) no-repeat -114px -130px!important;
    }
}

.oms-content {
    table {
        thead {
            th {
                text-align: left;
            }
        }
    }
    .search-content {
        padding: 5px 20px;
        p {
            margin: 0;
            font-size: 12px;
            color: #737373;
        }
        .btns-group {
            margin-top: 8px;
            margin-bottom: 0;
            button {
                margin-right: 15px;
            }
        }
    }
    .list-content {
        padding: 20px;
        padding-top: 0;
        .checkboxRow {
            padding: 8px 18px;
        }
        .btn-toolbar {
            position: relative;
            padding: 0;
        }
    }
}

.oms-detail {
    min-height: 700px;
    padding: 20px;
    table {
        margin-bottom: 0;
        tr {
            >th {
                text-align: left;
            }
            td {
                height: 24px;
                line-height: 24px;
            }
        }
    }
    .btns-group {
        button {
            padding: 4px 15px;
            margin-right: 18px;
        }
    }
    .supplierr-info {
        margin-top: 20px;
    }
    .purchase-info {
        width: 100%;
        margin-top: 20px;
        border: 1px solid #e1e2e7;
        .fs14 {
            font-size: 14px;
        }
        .purchase-title {
            height: 40px;
            line-height: 40px;
            span {
                float: left;
                margin-left: 20px;
                font-size: 14px;
                color: #333;
            }
            a {
                float: right;
                margin-right: 20px;
                font-size: 14px;
            }
        }
        table {
            .info-tr {
                border-bottom: 1px solid #f6f7fc;
            }
            .p-content {
                font-size: 14px;
                line-height: 18px;
                color: #999;
            }
            .product-info {
                width: 242px;
                height: 82px;
                padding: 10px;
                border: 1px solid #eee;
                img {
                    float: left;
                    margin-right: 10px;
                }
                .product-detail {
                    float: left;
                    p {
                        width: 150px;
                        height: 34px;
                        margin-bottom: 10px;
                        overflow: hidden;
                        font-size: 12px;
                        line-height: 17px;
                        color: #737373;
                    }
                    a {
                        font-size: 12px;
                        color: #737373;
                        text-decoration: underline !important;
                    }
                }
            }
            .attachment-list {
                margin: 0;
                li {
                    float: left;
                    margin-right: 20px;
                    span {
                        float: left;
                        width: 14px;
                        height: 16px;
                        margin: 4px;
                        background: url(/view/images/ico_attchment.png) 0 0 no-repeat;
                    }
                    .icon-attaType-doc {
                        background-position: 0 0;
                    }
                    .icon-attaType-img {
                        background-position: -26px 0;
                    }
                    .icon-attaType-ppt {
                        background-position: -78px 0;
                    }
                    .icon-attaType-pdf {
                        background-position: -104px 0;
                    }
                    .icon-attaType-xls {
                        background-position: -52px 0;
                    }
                    a {
                        float: left;
                        height: 24px;
                        line-height: 24px;
                    }
                }
            }
        }
    }
    .audit-info {
        margin-top: 20px;
    }
}

.oms-product {
    .pd-c {
        label {
            text-indent: -9999px;
        }
