.header {
    position: fixed;
    z-index: 1000;
    width: 100%;
    height: 51px !important;
    background-color: #fff;
    border-bottom: 1px solid #e1e2e7;
    .header-center {
        position: relative;
        width: 100%;
        height: 50px;
        padding-left: 220px;
        background-color: transparent;
        .sidebar {
            position: absolute;
            top: 0;
            left: 0;
            width: 220px;
            height: 50px;
            overflow: hidden;
            background-color: #ed4d4d;
            .oms-logo {
                width: 220px;
                height: 28px;
                margin-top: 11px;
                background: #ed4d4d url(/view/images/oms-logo.png) center 0 no-repeat;
            }
        }
        .top {
            width: 100%;
            height: 50px;
            padding: 0 20px;
            margin: 0;
            background-color: transparent;
            .top-left {
                float: left;
                height: 50px;
                line-height: 50px;
                i {
                    float: left;
                    margin-right: 10px;
                    font-size: 20px;
                    color: #bababf;
                    cursor: pointer; // display: none;
                }
                span {
                    float: left;
                    margin-right: 20px;
                    font-size: 14px;
                    color: #474748;
                    em {
                        font-style: normal;
                        font-weight: normal;
                    }
                }
                p {
                    float: left;
                    margin: 0;
                    font-size: 12px;
                    color: #999;
                }
            }
            .top-right {
                float: right;
                .oms-user {
                    position: relative;
                    float: left;
                    height: 50px;
                    line-height: 50px;
                    cursor: default;
                    &:hover {
                        .info-up {
                            display: inline-block;
                        }
                        .info-down {
                            display: none;
                        }
                        .info-op {
                            display: block;
                        }
                    }
                    img {
                        float: left;
                        width: 32px;
                        height: 32px;
                        margin: 9px 10px 0 0;
                        border-radius: 3px;
                    }
                    >span {
                        float: left;
                        margin-right: 10px;
                        font-size: 14px;
                        color: #333;
                    }
                    >i {
                        float: left;
                        font-size: 20px;
                        color: #bababf;
                    }
                    .info-up {
                        display: none;
                    }
                    .info-op {
                        position: absolute;
                        top: 46px;
                        left: 30px;
                        display: none;
                        width: 120px;
                        border: 1px solid #abafb3;
                        .info-content {
                            position: relative;
                            ul {
                                margin: 0;
                                li {
                                    height: 30px;
                                    padding-left: 16px;
                                    font-size: 14px;
                                    line-height: 30px;
                                    cursor: pointer;
                                    background-color: #fff;
                                    &:hover {
                                        background-color: #f6f7fc;
                                    }
                                    i {
                                        margin-right: 10px;
                                        font-size: 14px;
                                        color: #737373;
                                    }
                                }
                            }
                            .icon-top {
                                position: absolute;
                                top: -6px;
                                left: 92px;
                                width: 9px;
                                height: 6px;
                                background: #fff url(/view/images/icons.png) -68px -1px no-repeat;
                            }
                        }
                    }
                }
            }
            &:before,
            &:after {
                display: table;
                content: ' ';
            }
            &:after {
                clear: both;
            }
        }
    }
}

