                        border-left: 1px solid #f1f2f7;
                    }
                    span {
                        display: inline-block;
                        width: 32px;
                        height: 32px;
                        font-size: 20px;
                        line-height: 32px;
                        color: #737373;
                        text-align: center;
                        border: 2px solid #fff;
