.checkbox-content {
    width: 630px;
    label {
        float: left;
        width: 195px;
        height: 30px;
        padding: 0;
        margin: 0;
        line-height: 30px;
    }
    input[type='checkbox'] {
        width: 14px;
        height: 14px;
        padding: 0;
        margin-top: 8px !important;
        margin-right: 5px !important;
    }
    &:before,
    &:after {
        display: table;
        content: " ";
    }
    &:after {
        clear: both;
    }
}

.file-list {
    display: block;
    padding: 10px 0;
    vertical-align: middle;
    >li {
        float: left;
        margin-right: 20px;
        span,
        p,
        i {
            display: inline-block;
            float: left;
            height: 24px;
            font-style: normal;
            font-weight: normal;
            line-height: 24px;
        }
        span {
            float: left;
            width: 14px;
            height: 16px;
            margin: 4px 10px 0 0;
            background: url(/view/images/ico_attchment.png) 0 0 no-repeat;
        }
        .icon-attaType-doc {
            background-position: 0 0;
        }
        .icon-attaType-img {
            background-position: -26px 0;
        }
        .icon-attaType-ppt {
            background-position: -78px 0;
        }
        .icon-attaType-pdf {
            background-position: -104px 0;
        }
        .icon-attaType-xls {
            background-position: -52px 0;
        }
        .icon-attaType-rm {
            background-position: -130px 0;
        }
        p {
            margin: 0 5px 0 0;
            font-size: 14px;
            color: #333;
        }
        i {
            font-size: 12px;
            color: #666;
            cursor: pointer;
            &:hover {
                color: #e50000;
            }
        }
    }
    &:before,
    &:after {
        display: table;
        content: ' ';
    }
    &:after {
        clear: both;
    }
}

