// 带选择功能的下拉框
.chose-hide {
    position: absolute!important;
    top: -999em!important;
}

.chose-container {
    position: relative;
    float: left;
    padding: 0!important;
    margin-right: 40px;
    border: none!important;
    .chose-single {
        display: inline-block;
        width: 100%;
        height: 30px;
        padding: 6px 12px;
        color: #333;
        border: 1px solid #eee;
        &::after {
            position: absolute;
            top: 12px;
            right: 8px;
            content: '';
            border-style: solid;
            border-width: 6px 3px;
            border-right-color: transparent;
            border-bottom-color: transparent;
            /* border-top-color: transparent; */
            border-left-color: transparent;
        }
        i {
            right: 8px;
            float: right;
            width: 12px;
            height: 12px;
            font-size: 12px;
            background-color: #eee;
        }
    }
    .chose-drop {
        position: absolute;
        z-index: 1000;
        width: 195px;
        background-color: #fff;
        border: 1px solid #eee;
    }
    .chose-search {
        input[type='text'] {
            float: none;
            width: 100%;
            height: 30px;
            padding-left: 12px;
            margin: 0;
            border: 1px solid #ccc;
        }
    }
    .chose-result {
        max-height: 370px;
        overflow-x: hidden;
        overflow-y: scroll;
        li {
            padding: 5px 12px;
            list-style-type: none;
            &:hover {
                background-color: #e1e2e7;
            }
        }
    }
}

