/*输入框自动保存*/

.autoInput-parent {
    position: relative;
    .autoInput-container {
        position: absolute;
        left: 0;
        z-index: 10;
        width: 326px;
        .autoInput-add {
            position: absolute;
            top: -30px;
            width: 32px;
            line-height: 30px;
            text-decoration: none;
        }
        ul {
            max-height: 240px;
            margin-bottom: 0;
            overflow-x: hidden;
            overflow-y: auto;
            background: #fff;
            border: 1px solid #abafb3;
            li {
                position: relative;
                min-height: 30px;
                padding: 4px 0;
                line-height: 22px;
                span {
                    position: relative;
                    z-index: 1;
                    display: block;
                    padding: 0 20px 0 10px;
                    cursor: pointer;
                }
                .del {
                    position: absolute;
                    top: 0;
                    right: 5px;
                    z-index: 2;
                    display: none;
                    font-size: 12px;
                    line-height: 30px;
                    color: #bec3c7;
                    text-decoration: none;
                    &:hover {
                        color: #d54545;
                    }
                }
                &:hover {
                    background: #f6f7fc;
                    .del {
                        display: block;
                    }
                }
            }
        }
    }
}










